* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  font-size: 1rem;
  text-decoration: none;
  font-family: 'Cairo', sans-serif; }

*:before,
*:after {
  box-sizing: inherit; }

body {
  overflow-x: hidden; }

.header__text {
  display: none; }

.progressbar {
  height: 2em;
  width: 85%;
  margin-top: 1.5em;
  background-color: #fff;
  border-radius: 25px;
  border: 10px solid #1A519E;
  margin-left: 10%; }

.numerotation {
  display: flex;
  justify-content: space-between; }
  .numerotation p {
    font-size: 2em;
    font-weight: 700;
    color: #1A519E; }

.relative {
  position: relative;
  width: 100%; }

#file {
  margin-top: 1.5em;
  width: 83.1%;
  margin-left: 10%;
  position: absolute;
  top: 8px;
  right: 5.9%; }

.main__inform {
  text-align: center;
  margin-bottom: 3%;
  padding-top: 7em; }
  .main__inform__img {
    width: 2.5em;
    height: 2.5em; }
    .main__inform__img--active {
      border: 10px solid #1078ad;
      border-radius: 50%; }
  .main__inform a {
    color: white;
    padding: 0.6% 3%;
    background-color: #1078ad;
    border-radius: 5px;
    font-size: 2em;
    font-weight: 700; }

.main__tags {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: 3% auto; }

.main__tag h3 {
  color: #1078ad;
  font-weight: 700; }

.main__Preambule {
  width: 60%;
  margin: 2% auto;
  background-color: #96C5DC;
  padding: 3% 2% 4%;
  border-radius: 5px; }
  .main__Preambule h3 {
    color: #1078ad;
    font-weight: 700;
    font-size: 2em; }
  .main__Preambule p {
    margin: 2% 0; }

.main__questionnaire {
  text-align: center;
  padding-top: 7em; }

.main__Resultat {
  padding-top: 7em;
  text-align: center; }

.affiche__resultat {
  max-width: 65%;
  margin: 2em auto;
  padding: 2em 3em;
  background-color: #96C5DC;
  border-radius: 10px; }
  .affiche__resultat h3 {
    font-size: 2em;
    font-weight: 700;
    color: #1A519E; }
  .affiche__resultat p {
    font-size: 1.5em;
    font-weight: 500;
    margin: 1em 0; }
  .affiche__resultat span {
    font-size: 0.9em;
    color: #1A519E; }

.affiche {
  display: none; }

form {
  border: 3px solid #96C5DC;
  border-radius: 1em;
  padding: 2em 4em;
  max-width: 65%;
  margin: 3em auto; }

.barNumber {
  border: 3px solid #96C5DC;
  border-radius: 1em;
  margin-bottom: 1em; }

.questions p {
  font-weight: 700;
  font-size: 1.5em;
  color: #7F7F7F;
  height: 15vh;
  padding-top: 5%; }

.reponces {
  height: 24vh;
  display: flex;
  padding-top: 5%;
  justify-content: space-evenly;
  flex-wrap: wrap; }
  .reponces label {
    margin-left: 0.4em;
    font-size: 2em; }
  .reponces .inputText {
    margin-left: 20%; }
  .reponces div {
    width: 45%; }

#repeter {
  border: 1px dotted #1A519E;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 1.5em;
  font-weight: 600;
  background-color: #1078ad;
  margin: 2em auto; }

.inputs {
  display: flex;
  justify-content: space-between; }
  .inputs #precedent,
  .inputs #suivant,
  .inputs #resultat {
    border: 1px dotted #1A519E;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 1.5em;
    font-weight: 600;
    background-color: #1078ad; }

.answer-inputs {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 25vh; }

.answer-inputs input[type="number"] {
  font-size: 16px;
  border: 1px solid #1078AD;
  width: 55%;
  padding: 10px; }

.answer-inputs .input-span {
  font-size: 22px;
  border: 1px solid #1078AD;
  border-left: none;
  padding: 4px 10px 5px;
  color: #555555;
  background-color: #edf6fa; }

.answer-inputs input[type="radio"] {
  -webkit-appearance: none; }

.answer-inputs input[type="radio"]:checked + label {
  background-color: #1078AD;
  color: #F4CF1B; }

.answer-inputs label {
  height: 80px;
  width: 120px;
  color: #1078AD;
  border-radius: 4px;
  border: 4px solid #1078AD;
  font-size: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 9px 0px 0;
  transition: .5s ease;
  margin-right: 50px; }

.answer-inputs label span {
  font-size: 22px;
  position: relative;
  bottom: 5px; }

input:disabled {
  cursor: not-allowed; }
